            steamyWeekScheduleAT211["Вівторок"][1].name="Пари нe буде";
            steamyWeekScheduleAT211["Вівторок"][1].link=null;

            steamyWeekScheduleAT211["Середа"][0].name=lessonById[10].lessonName;
            steamyWeekScheduleAT211["Середа"][0].link=lessonById[10].link;

            steamyWeekScheduleAT211["Четвер"][2].name=lessonById[5].lessonName + ' (Лабораторна)';
            steamyWeekScheduleAT211["Четвер"][2].link=lessonById[5].link;
            return steamyWeekScheduleAT211;
          }else{
            let steamyWeekScheduleNatLat = JSON.parse(JSON.stringify(scheduleNatLat));
    
            steamyWeekScheduleNatLat["Вівторок"][1].name="Пари нe буде";
            steamyWeekScheduleNatLat["Вівторок"][1].link=null;

            steamyWeekScheduleNatLat["Середа"][0].name=lessonById[10].lessonName;
            steamyWeekScheduleNatLat["Середа"][0].link=lessonById[10].link;

            steamyWeekScheduleNatLat["Четвер"][2].name=lessonById[6].lessonName + ' (Практика)';
            steamyWeekScheduleNatLat["Четвер"][2].link=lessonById[6].link;
            return steamyWeekScheduleNatLat;
          }
        }
      },
      getWeekNumber() {
        const today = new Date();
        const startOfYear = new Date(today.getFullYear(), 0, 1);
        let weekNumber = Math.ceil(((today - startOfYear) / 86400000 + startOfYear.getDay() + 1) / 7) - 9;
        if (today.getDay() === 0) {
          weekNumber -= 1;
        }
        return weekNumber;
      },
      getDay(){
        const daysOfWeek = ['неділя', 'понеділок', 'вівторок', 'середа', 'четвер', 'п`ятниця', 'субота'];
        const date = new Date();
        const currentDayOfWeek = daysOfWeek[date.getDay()];
        return currentDayOfWeek
      },
      

      
    },
    watch: {
      selectedOption(newSelectedOption) {
        localStorage.selectedOption = newSelectedOption;
      },
      showContent(newShowContent) {
        localStorage.showContent = newShowContent;
      },
      utc(newUtc) {
        localStorage.utc = newUtc;
      },
      discipline(newDiscipline) {
        localStorage.discipline = newDiscipline;
      },
    },
    mounted() {
      if (localStorage.selectedOption ) {
        this.selectedOption = localStorage.selectedOption;
      }
      if (localStorage.showContent) {
        this.showContent = localStorage.showContent === 'true';
      }
      if(localStorage.discipline){
        this.discipline = localStorage.discipline;
      }

      const initUserTheme = this.getTheme() || this.getMediaPreference();
      this.setTheme(initUserTheme);

      document.addEventListener('keydown', this.handleKeyDown);
    },
    methods: {
      
      handleKeyDown(event) {
      if (event.keyCode === 27) { 
        (this.burgerStatus) ? this.burgerStatus = false : this.burgerStatus = true;
      }
      },
      setTheme(theme) {
        localStorage.setItem("user-theme", theme);
        this.userTheme = theme;
        document.documentElement.className = theme;
        (theme === "light-theme") ? document.getElementById("switchTheme").checked = true 
          : document.getElementById("switchTheme").checked = false;
      },
      toggleTheme() {
        const activeTheme = localStorage.getItem("user-theme");
        (activeTheme === "light-theme") ? this.setTheme("dark-theme") : this.setTheme("light-theme");
      },
      getMediaPreference() {
        const hasDarkPreference = window.matchMedia("(prefers-color-scheme: dark)").matches;
        return (hasDarkPreference) ? "dark-theme" : "light-theme";
      },
      getTheme() {
        return localStorage.getItem("user-theme");
      },
      toggleBurger() {
        (this.burgerStatus) ? this.burgerStatus = false : this.burgerStatus = true;
      },
      toggleSettings() {
        (this.settingsStatus && this.burgerStatus) ? this.settingsStatus = false : this.settingsStatus = true;
      }
    },
    
};
</script>

